 

 body, html {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Roboto Slab', serif; 
  line-height: 1.8;
  background-color:azure;
 }
 
 
img {
  width: 100%;
  object-fit: contain;
  opacity: 0.8;
}

.textwrapper {
  width: 100%;
  position: relative;
  margin-top: -20%;
  text-align: center;
  color: wheat;
}
 ul {
   margin-bottom: 0;
 }
 h1, h2, h3, h4, p{
  margin: 0;
  padding: 0;
}
.mystyle {
  padding: 15px;
}

.introduction { 
  width: 50%;
  height: 100%;
  float: left;
  overflow: hidden;
  position: fixed;
}
.test {
  display: block;
  width: 50%;
  height: 100%;
  float: right;
 
}

.intro, .summary, .technologies, .skills, .experience, .education {
  display: grid;
  grid-template-columns: 1fr 4fr;
}


.mystyleflex {
  display: flex;
}
.thedate {
  margin-left: auto;
}
.myintro, .mysummary,.myskills, .myexperience, .myeducation, .mytarget {
  color: rgb(35, 107, 189);
  font-weight: 800;
}
.mytarget {
  margin: 0;
  padding: 0;
}
 
